<template>
<div class="page page__04 chapter04">
  <div class="content content-full flex justify-center  relative">
    <div class="slider">
      <swiper ref="slider" :options="swiperOptions">
        <swiper-slide>
          <div class="conversation container mx-auto flex flex-col flex-grow align-center justify-center">
            <div class="conversation__item gloria fade-up-staggered">
              <img src="@/assets/icons/gloria/question.svg">
              <p>Weißt du eigentlich, warum eine Pyramide unten breit ist<br>
                und nach oben immer schmaler wird?</p>
            </div>

            <div class="conversation__item paul fade-up-staggered">
              <img src="@/assets/icons/paul/question.svg">
              <p>Ist doch klar. Damit sie nicht umkippt.<br>
                Das Fundament gibt allem darüber Halt. Warum fragst du?</p>
            </div>

            <div class="conversation__item gloria fade-up-staggered">
              <img src="@/assets/icons/gloria/question.svg">
              <p>Weil der neue GLOBUS eine Markenpyramide hat.<br>
                Die funktioniert bestimmt genauso.</p>
            </div>

            <div class="conversation__item paul fade-up-staggered">
              <img src="@/assets/icons/paul/question.svg">
              <p>Das müssen wir uns genauer anschauen!</p>
            </div>
          </div>

        </swiper-slide>
        <swiper-slide>
          <img src="@/assets/global/header-tent.svg" class="header__tent">
          <div class="relative">
            <div class="badge badge__large mt-12">
              Die Spitze der<br>
              Markenpyramide.
            </div>
          </div>
          <div class="pl-badgestart flex flex-col flex-grow align-center fade-up">
            <div class="badge__spacer mt-10  xl:mt-20"></div>
            <div class="text__inner">
            <p class="mb-10 xl:mb-0">
              Ganz oben steht unsere Vision:<br>
              <strong>„Wir geben dem Guten ein Zuhause.“</strong><br class="wordspacer"><br class="wordspacer">

              Sie basiert auf unserer Markenessenz:<br>
                <strong>„Echt gut gemacht.“</strong>

            </p>
              </div>
          </div>
          <div class="slide__overlay"></div>
          <div class="slide__bg slide__bg-01"></div>
        </swiper-slide>
        <swiper-slide>
          <img src="@/assets/global/header-tent.svg" class="header__tent">

          <div class="pl-badgestart flex flex-col flex-grow align-center fade-up">
            <div class="mt-10 xl:mt-40"></div>
            <div class="text__inner large">
            <p class="mb-10 xl:mb-0">
              Das ist nicht nur ein Versprechen an unsere Kunden,<br>
              sondern auch unser Anspruch an uns selbst. Damit<br>
              setzen wir uns selbst einen hohen Standard für alles,<br>
              was wir tun – und was wir sind:<br class="wordspacer"><br class="wordspacer">

              <strong>Die Markthalle für eine ausgewählte Vielfalt echt gut<br>
              gemachter Produkte, Erlebnisse und Services.</strong><br class="wordspacer"><br class="wordspacer">

              Das schaffen wir alle gemeinsam dank unserer Leistungen.<br>
              Und die sind das Fundament!

            </p>
              </div>
          </div>
          <div class="slide__overlay"></div>
          <div class="slide__bg slide__bg-02"></div>
        </swiper-slide>
        <swiper-slide>
<!--          <img src="@/assets/global/header-tent.svg" class="header__tent">-->

          <div class="conversation container mx-auto flex flex-col flex-grow align-center justify-center mb-28 xl:mb-0">
            <div class="conversation__item gloria fade-up-staggered">
              <img src="@/assets/icons/gloria/question.svg">
              <p>So sieht sie also aus, die GLOBUS-Markenpyramide. Ziemlich cool. Aber sie funktioniert nur,<br>
                wenn der untere Teil richtig stark und stabil ist.</p>
            </div>
            <div class="fade-up-staggered flex justify-center">
              <div class="relative lightbox__wrapper">
                <img src="@/assets/chapter04/markenpyramide.svg" class="markenpyramide">
                <div class="lightbox__pyramide" v-on:click="lightboxOpen = !lightboxOpen">
                  <img src="@/assets/icons/Icon-Lupe.svg">
                  <p>Zum Vergrößern klicken</p>
                </div>
              </div>

            </div>
            <div></div>
            <div class="conversation__item paul mr-24 fade-up-staggered">
              <img src="@/assets/icons/paul/question.svg">
              <p>Auf jeden Fall! Wir nehmen das gleich mal unter die Lupe.</p>
            </div>
          </div>
          <div class="chapter__end fade-up-staggered">
            <router-link to="/overview" class="end__btn to_overview">Zur Übersicht</router-link>
            <router-link to="/chapter05" class="end__btn to_chapter"  @click.native="setStatus(0.6)">Zum nächsten Kapitel</router-link>
          </div>
        </swiper-slide>
        <div class="swiper-button-prev" slot="button-prev"></div>
        <div class="swiper-button-next" slot="button-next"></div>

      </swiper>
      <div class="swiper-pagination" slot="pagination"></div>
    </div>

  </div>
  <div class="footer__border"></div>
  <div class="ligthbox" :class="{'open': lightboxOpen}">
    <div class="lightbox--inner">
      <div class="ligthbox--close" v-on:click="lightboxOpen = false"></div>
      <swiper ref="slider2" :options="swiperOptions2">
        <swiper-slide>
          <div class="container ">
            <img src="@/assets/chapter04/Pyramide4-unten.svg" class="markenpyramide">
          </div>
        </swiper-slide>
        <swiper-slide>
          <div class="container ">
            <img src="@/assets/chapter04/Pyramide3-mitte.svg" class="markenpyramide">
          </div>
        </swiper-slide>
        <swiper-slide>
          <div class="container ">
            <img src="@/assets/chapter04/Pyramide2-mitte.svg" class="markenpyramide">
          </div>
        </swiper-slide>
        <swiper-slide>
          <div class="container ">
            <img src="@/assets/chapter04/Pyramide1-oben.svg" class="markenpyramide">
          </div>
        </swiper-slide>
        <div class="swiper-button-prev swiper-button-prev2" slot="button-prev"></div>
        <div class="swiper-button-next swiper-button-next2" slot="button-next"></div>

      </swiper>

    </div>

  </div>
</div>
</template>

<script>

import {gsap} from "gsap";
import ltiService from "@/services/ltiService";

export default {
  name: "Page01",
  data() {
    return {
      lightboxOpen: false,
      swiperOptions: {
        allowTouchMove: false,
        slidesPerView: 1,
        centeredSlides: true,
        autoHeight: true,
        pagination: {
          clickable: true,
          el: '.swiper-pagination'
        },
        navigation: {
          nextEl: '.swiper-button-next',
          prevEl: '.swiper-button-prev'
        }
      },
      swiperOptions2: {
        allowTouchMove: false,
        slidesPerView: 1,
        centeredSlides: true,
        autoHeight: true,
        navigation: {
          nextEl: '.swiper-button-next2',
          prevEl: '.swiper-button-prev2'
        }
      }
    }
  },
  computed: {
    swiper() {
      return this.$refs.slider.$swiper
    },
    name(){
      return localStorage.getItem("name");
    }
  },
  methods:{
    setStatus(value){
      let s = window.localStorage.getItem('status');
      s = parseFloat(s);
      if(s < value){
        window.localStorage.setItem('status', value);
        ltiService.setStatus(value)
            .then(response => {
              console.log("Set status", response);
            })
            .catch(error => {
              console.log("There was an error:", error.response);
            });
        // ApiCall
      }
    }
  },
  mounted() {

    // Set Animation up
    let fade = document.querySelectorAll('.fade-up');
    gsap.set(fade, {y: 80, opacity: 0});
    let fadeStagger = document.querySelectorAll('.fade-up-staggered');
    gsap.set(fadeStagger, {y: 80, opacity: 0});
    let badge = document.querySelectorAll('.badge');
    gsap.set(badge, {x: -80, opacity: 0});


    // Call Animation for first Slide
    let activeFade = document.querySelectorAll('.swiper-slide-active .fade-up');
    let activeFadeStagger = document.querySelectorAll('.swiper-slide-active .fade-up-staggered');
    let activeBadge = document.querySelectorAll('.swiper-slide-active .badge');
    gsap.to(activeFade,  {opacity: 1, y: 0, duration: 1.35});
    gsap.to(activeFadeStagger,  {opacity: 1, y: 0, duration: 1.35, stagger: 2.25});
    gsap.to(activeBadge,  {opacity: 1, x: 0, duration: 1.35});


    // Call Animation on slide Change
    this.swiper.on('slideChangeTransitionEnd', function(){


      let badge = document.querySelectorAll('.swiper-slide-active .badge');
      gsap.to(badge, {opacity: 1, x: 0, duration: 0.5});

      let fadeUps = document.querySelectorAll('.swiper-slide-active .fade-up');
      gsap.to(fadeUps, {opacity: 1, y: 0, duration: 0.5});

      let fadeUpsStagger = document.querySelectorAll('.swiper-slide-active .fade-up-staggered');
      gsap.to(fadeUpsStagger, {opacity: 1, y: 0, duration: 1.35, stagger: 1.5});
    })
  }
}
</script>